import styled from 'styled-components';

const Button = styled.button`
  background-color: black;
  color: white;
  border: none;
  border-radius: 0.3rem;
  font-weight: bold;
  margin: 0.5rem 0.2rem;
  padding: 0.3rem 0.6rem;
  outline: 0;
`;

export default Button;



